import React from 'react'
import Login from "../Components/Login";
import {fireEvent, render} from '@testing-library/react-native'

describe('Login Screen', () => {
    it('Should go to home page on login', () => {
        const navigation = {navigate : () => {}}
        jest.spyOn(navigation, "navigate").mockReturnValue(of(validationResponseMock));
        const page = render(<Login navigation={navigation}></Login>);
        const LoginBtn = page.queryByTestId("LoginBtn")
        fireEvent.press(LoginBtn);
        expect(navigation.navigate).toBeTruthy("Root");
    })
})